<template>
    <v-row justify="center">
        <v-col cols="10">
            <br>
            <v-card>
                <v-card-title>
                      ( {{ Lessons.length }})
                    <v-spacer></v-spacer>
                    <v-text-field v-model="Query" @keyup.enter="SearchLesson" label=" " append-icon="mdi-magnify"></v-text-field>
                </v-card-title>
                <v-simple-table>
                    <template v-slot:default>
                        <thead>
                        <tr>
                            <th class="text-left">#</th>
                            <th class="text-left"></th>
                        </tr>
                        </thead>
                        <tbody>

                        <tr v-if="fetchingLessons">
                            <v-progress-linear indeterminate></v-progress-linear>
                        </tr>
                        <tr v-else v-for="(lesson, index) in Lessons" v-bind:key="lesson._id">
                            <td>{{ index }}</td>
                            <td>{{ lesson.type }}</td>
                        </tr>
                        </tbody>
                    </template>
                </v-simple-table>
            </v-card>
            <br>
        </v-col>
        <v-col cols="10">
            <v-card>
                <v-card-title> </v-card-title>
                <v-card-text>
                    <v-text-field label="" v-model="Lesson.type"></v-text-field>
                </v-card-text>
                <v-card-actions>
                    <Button v-bind:content="``" v-bind:text="true" v-on:click.native="AddLesson"></Button>
                </v-card-actions>
            </v-card>
            <br>
        </v-col>
    </v-row>
</template>

<script>
    import LessonService from '../../service/lessons.js';
    import Button from '../form/Button.vue';

    export default {
        name: 'Lesson',
        data() {
            return {
                Query: '',
                Lessons: [],
                Lesson: {
                    type: '',
                },
                fetchingLessons: false,
            };
        },
        methods: {
            SearchLesson() {
                this.fetchingLessons = true;
                if (this.Query === '') {
                    this.$notify({
                        title: ' .',
                        text: ' ',
                        type: 'warn',
                    });
                    this.fetchingLessons = false;
                    return;
                }
                LessonService.getLessons(this.Query)
                    .then(response => {
                        this.Lessons = response.data.Lessons;
                        this.fetchingLessons = false;
                    })
                    .catch(() => {
                        this.$notify({
                            title: ' .',
                            type: 'error',
                        });
                    });
            },
            AddLesson: function () {
                if (this.Lesson.type === '') {
                    this.$notify({
                        title: '    .',
                        type: 'error',
                    });
                    return;
                }
                this.Lesson.type = this.Lesson.type.trim();
                LessonService.addLesson(this.Lesson)
                    .then(response => {
                        if (response.crash === true) {
                            this.$notify({
                                title: '  .',
                                type: 'warn',
                            });
                        } else {
                            this.$notify({
                                title: ' .',
                                type: 'success',
                            });
                        }
                        this.Lesson = {
                            type: '',
                        };
                    })
                    .catch(() => {
                        this.$notify({
                            title: ' .',
                            type: 'error',
                        });
                    });
            },
        },
        components: {
            Button
        },
    };
</script>
