<template>
    <v-row justify="center">
        <v-col cols="10">
            <v-card md-with-hover>
                <v-card-title>
                      ( {{ Students.length }})
                    <v-spacer></v-spacer>
                    <v-text-field v-model="Query" @keyup.enter="SearchStudents" label="" append-icon="mdi-magnify"></v-text-field>
                </v-card-title>
                <v-simple-table>
                    <template v-slot:default>
                        <thead>
                        <tr>
                            <th class="text-left">#</th>
                            <th class="text-left"></th>
                            <th class="text-left"> / </th>
                            <th class="text-left">unitstudyId</th>
                            <th class="text-left"></th>
                        </tr>
                        </thead>
                        <tbody>

                        <tr v-if="fetchingStudents">
                            <v-progress-linear indeterminate></v-progress-linear>
                        </tr>
                        <tr v-else v-for="(student, index) in Students" v-bind:key="student._id">
                            <td>{{ index }}</td>
                            <td><a href="javascript:void(0)" @click="StudentClick(student._id)">{{ student.name }}</a></td>
                            <td>{{ student.school + '/' + student.grade }}</td>
                            <td>{{ student.unitStudyId }}</td>
                            <td><a href="javascript:void(0)" @click="RemoveStudent(student._id)"></a></td>
                        </tr>
                        </tbody>
                    </template>
                </v-simple-table>
            </v-card>
            <br/>
        </v-col>
        <v-col cols="10">
            <v-card>
                <v-card-title></v-card-title>
                <v-card-text>
                    <v-row>
                        <v-col cols="6" md="4">
                            <v-text-field label="" v-model="Student.name"></v-text-field>
                        </v-col>
                        <v-col cols="6" md="4">
                            <v-text-field label="" v-model="Student.school"></v-text-field>
                        </v-col>
                        <v-col cols="6" md="4">
                            <v-text-field label="" v-model="Student.grade"></v-text-field>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="6" md="4">
                            <v-text-field label=" ID" v-model="Student.unitStudyId"></v-text-field>
                        </v-col>
                        <v-col cols="6" md="4">
                            <v-text-field label="" v-model="Student.phone"></v-text-field>
                        </v-col>
                        <v-col cols="6" md="4">
                            <v-text-field label=" " v-model="Student.parent_phone"></v-text-field>
                        </v-col>
                    </v-row>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <Button v-bind:text="true" v-bind:content="``" v-on:click.native="AddStudent"></Button>
                </v-card-actions>
            </v-card>
            <br/>
        </v-col>
    </v-row>
</template>

<script>
    import StudentService from '../../service/students.js';
    import Button from '../form/Button.vue';

    export default {
        name: 'Students',
        data() {
            return {
                Query: '',
                fetchingStudents: false,
                Students: [],
                Student: {
                    name: '',
                    school: '',
                    grade: '',
                    unitStudyId: '',
                    phone: '',
                    parent_phone: '',
                },
            };
        },
        methods: {
            SearchStudents() {
                this.fetchingStudents = true;
                if (this.Query === '') {
                    this.$notify({
                        title: ' .',
                        text: ' ',
                        type: 'warn',
                    });
                    this.fetchingStudents = false;
                    return;
                }
                StudentService.getStudents(this.Query)
                    .then((response) => {
                        this.Students = response.data.students;
                        this.fetchingStudents = false;
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            },
            AddStudent() {
                StudentService.addStudent(this.Student)
                    .then((response) => {
                        this.$notify({
                            title: response.data.msg,
                            text: '.',
                            type: 'success',
                        });
                        this.Student = {
                            name: '',
                            school: '',
                            grade: '',
                            unitStudyId: '',
                            phone: '',
                            parent_phone: '',
                        };
                    })
                    .catch(err => {
                        console.log(err);
                    });
            },
            StudentClick(StudentId) {
                const NextDestination = '/student/' + StudentId;
                this.$router.push(NextDestination);
            },
            RemoveStudent(StudentId) {
                return StudentId;
            },
        },
        components: {
            Button,
        },
    };
</script>
